  .axis path,
  .axis line {
      fill: none;
      stroke: #000;
      shape-rendering: crispEdges;
  }

  .x.axis path {
      display: none;
  }

  .line {
      fill: none;
      stroke-width: 1.5px;
  }

  .overlay {
      fill: none;
      pointer-events: all;
  }

  .focus circle {
      fill: #f2a808;
  }

  .focus text {
      font-size: 14px;
  }

  .tooltip {
      fill: white;
      stroke: #000;
  }

  .tooltip-date, .tooltip-likes {
      font-weight: bold;
  }

